import { UmbControllerBase } from '@umbraco-cms/backoffice/class-api';
import {
	UMB_WORKSPACE_CONTEXT,
	UmbWorkspaceContext,
} from '@umbraco-cms/backoffice/workspace';
import uSyncExporter from '../constants';
import { UmbContextToken } from '@umbraco-cms/backoffice/context-api';
import { UmbControllerHost } from '@umbraco-cms/backoffice/controller-api';
import { ExporterSelectedItem } from '../api';
import SyncExporterService from '../shared/exporter.service';

export class SyncExporterWorkspaceContext
	extends UmbControllerBase
	implements UmbWorkspaceContext
{
	public readonly workspaceAlias: string = uSyncExporter.workspace;

	#service: SyncExporterService;

	getEntityType(): string {
		return uSyncExporter.entity;
	}

	constructor(host: UmbControllerHost) {
		super(host);

		this.provideContext(USYNC_EXPORTER_WORKSPACE_CONTEXT, this);
		this.provideContext(UMB_WORKSPACE_CONTEXT, this);

		this.#service = new SyncExporterService(host);
	}

	async GetSettings() {
		return await this.#service.GetSettings();
	}

	async GetExporters() {
		return await this.#service.GetExporters();
	}

	async GetItemInfo(items: ExporterSelectedItem[]) {
		return await this.#service.GetItemInfo(items);
	}
}

export default SyncExporterWorkspaceContext;

export const USYNC_EXPORTER_WORKSPACE_CONTEXT =
	new UmbContextToken<SyncExporterWorkspaceContext>('SyncExporterWorkspaceContext');
